:root .neo-theme-neo-light {
  --neo-background-color: #fafafa;
  --neo-color: #000;
  --neo-disabled-opacity: 0.5;
  --neo-font-family: "Source Sans 3", sans-serif;
  --neo-font-smoothing: antialiased;
}

:root .neo-theme-neo-light {
  --black: #000000;
  --borderradius-0: 0px;
  --borderradius-4: 4px;
  --borderradius-8: 8px;
  --core-fontfamily-mono: "Source Code Pro";
  --core-fontfamily-sans: "Source Sans 3";
  --core-fontfamily-serif: "Source Serif Pro";
  --core-fontsize-body: 16px;
  --core-fontsize-h1: 2.5rem;
  --core-fontsize-h2: 1.75rem;
  --core-fontweight-bold: 700;
  --core-fontweight-medium: 500;
  --core-fontweight-regular: 400;
  --core-fontweight-semibold: 600;
  --core-lineheight-headline: 1.2;
  --core-lineheight-paragraph: 1.5;
  --gray-100: #F0F2F0;
  --gray-200: #D3D7D2;
  --gray-300: #B6BDB4;
  --gray-400: #99A295;
  --gray-50: #fafafa;
  --gray-500: #7C8877;
  --gray-600: #606A5D;
  --gray-700: #454B42;
  --gray-800: #292D28;
  --gray-900: #0E0F0D;
  --green-100: #bbfce4;
  --green-200: #a3fbda;
  --green-300: #8afad1;
  --green-400: #72f8c7;
  --green-50: #d4fdee;
  --green-500: #29F5AA;
  --green-600: #0ae192;
  --green-700: #08b072;
  --green-800: #067f53;
  --green-900: #044e33;
  --height-16: 16px;
  --height-2: 2px;
  --height-32: 32px;
  --height-4: 4px;
  --height-48: 48px;
  --height-56: 56px;
  --height-8: 8px;
  --purple-100: #efe3ff;
  --purple-200: #d0aaff;
  --purple-300: #b071ff;
  --purple-400: #9039ff;
  --purple-500: #7100ff;
  --purple-600: #5800c6;
  --purple-700: #3f008e;
  --purple-800: #260055;
  --purple-900: #0d001c;
  --spacing-12: 12px;
  --spacing-16: 16px;
  --spacing-2: 2px;
  --spacing-20: 20px;
  --spacing-24: 24px;
  --spacing-32: 32px;
  --spacing-4: 4px;
  --spacing-40: 40px;
  --spacing-48: 48px;
  --spacing-64: 64px;
  --spacing-8: 8px;
  --spacing-80: 80px;
  --spacing-96: 96px;
  --Transparent: rgba(255,255,255,0);
  --white: #FFFFFF;
}

:root .neo-theme-neo-light {
  --sem-borderradius-large: var(--borderradius-8);
  --sem-borderradius-medium: var(--borderradius-4);
  --sem-borderradius-none: var(--borderradius-0);
  --sem-borderwidth-focus: 3px;
  --sem-color-bg-neutral-active: var(--gray-300);
  --sem-color-bg-neutral-default: var(--gray-50);
  --sem-color-bg-neutral-disabled: var(--gray-200);
  --sem-color-bg-neutral-highlighted: var(--white);
  --sem-color-bg-neutral-hover: var(--gray-200);
  --sem-color-bg-primary-active: var(--green-600);
  --sem-color-bg-primary-background: var(--green-50);
  --sem-color-bg-primary-default: var(--green-400);
  --sem-color-bg-primary-disabled: var(--gray-200);
  --sem-color-bg-primary-hover: var(--green-500);
  --sem-color-border-contrast: var(--black);
  --sem-color-border-default: var(--gray-200);
  --sem-color-border-focus: #5595F5;
  --sem-color-border-subtle: var(--gray-100);
  --sem-color-fg-neutral-contrast: var(--black);
  --sem-color-fg-neutral-default: var(--gray-900);
  --sem-color-fg-neutral-disabled: var(--gray-300);
  --sem-color-fg-neutral-subdued: var(--gray-700);
  --sem-color-fg-primary-base: var(--green-500);
  --sem-color-fg-primary-faded: var(--green-600);
  --sem-color-fg-primary-highlighted: var(--green-400);
  --sem-height-large: var(--height-32);
  --sem-height-medium: var(--height-16);
  --sem-height-small: var(--height-8);
  --sem-height-xlarge: var(--height-48);
  --sem-height-xsmall: var(--height-4);
  --sem-height-xxlarge: var(--height-56);
  --sem-height-xxsmall: var(--height-2);
  --sem-spacing-large: var(--spacing-20);
  --sem-spacing-medium: var(--spacing-16);
  --sem-spacing-small: var(--spacing-12);
  --sem-spacing-xlarge: var(--spacing-24);
  --sem-spacing-xsmall: var(--spacing-8);
  --sem-spacing-xxlarge: var(--spacing-32);
  --sem-spacing-xxsmall: var(--spacing-4);
  --sem-spacing-xxxlarge: var(--spacing-40);
  --sem-spacing-xxxsmall: var(--spacing-2);
  --sem-typo-body-fontFamily: var(--core-fontfamily-sans);
  --sem-typo-body-fontSize: var(--core-fontsize-body);
  --sem-typo-body-fontWeight: var(--core-fontweight-regular);
  --sem-typo-body-letterSpacing: 0;
  --sem-typo-body-lineHeight: var(--core-lineheight-paragraph);
  --sem-typo-body-paragraphIndent: 0;
  --sem-typo-body-paragraphSpacing: 0;
  --sem-typo-body-textDecoration: none;
  --sem-typo-h1-fontFamily: var(--core-fontfamily-sans);
  --sem-typo-h1-fontSize: var(--core-fontsize-h1);
  --sem-typo-h1-fontWeight: var(--core-fontweight-bold);
  --sem-typo-h1-letterSpacing: -0.02em;
  --sem-typo-h1-lineHeight: var(--core-lineheight-headline);
  --sem-typo-h1-textDecoration: none;
  --sem-typo-h2-fontFamily: var(--core-fontfamily-sans);
  --sem-typo-h2-fontSize: var(--core-fontsize-h2);
  --sem-typo-h2-fontWeight: var(--core-fontweight-semibold);
  --sem-typo-h2-lineHeight: var(--core-lineheight-headline);
  --sem-typo-label-medium-fontFamily: var(--core-fontfamily-sans);
  --sem-typo-label-medium-fontSize: var(--core-fontsize-body);
  --sem-typo-label-medium-fontWeight: var(--core-fontweight-medium);
  --sem-typo-label-regular-fontFamily: var(--core-fontfamily-sans);
  --sem-typo-label-regular-fontSize: var(--core-fontsize-body);
  --sem-typo-label-regular-fontWeight: var(--core-fontweight-regular);
}

:root .neo-theme-neo-light {
  --cmp-breadcrumb-arrow: var(--sem-color-fg-neutral-default);
  --cmp-breadcrumb-currentpage: var(--sem-color-fg-primary-base);
  --cmp-breadcrumb-gap: var(--sem-spacing-xxsmall);
  --cmp-breadcrumb-pagelevel: var(--sem-color-fg-neutral-default);
  --cmp-button-borderradius: var(--sem-borderradius-large);
  --cmp-button-borderwidth: 1px;
  --cmp-button-gap: var(--sem-spacing-xxsmall);
  --cmp-button-ghost-bg-active: var(--sem-color-bg-neutral-active);
  --cmp-button-ghost-bg-default: transparent;
  --cmp-button-ghost-bg-disabled: var(--sem-color-bg-neutral-disabled);
  --cmp-button-ghost-bg-hover: var(--sem-color-bg-neutral-hover);
  --cmp-button-ghost-bg-loading: var(--sem-color-bg-neutral-hover);
  --cmp-button-ghost-fg-default: var(--sem-color-fg-neutral-contrast);
  --cmp-button-ghost-fg-disabled: var(--sem-color-fg-neutral-disabled);
  --cmp-button-height: var(--height-48);
  --cmp-button-primary-bg-active: var(--sem-color-bg-primary-active);
  --cmp-button-primary-bg-default: var(--sem-color-bg-primary-default);
  --cmp-button-primary-bg-disabled: var(--sem-color-bg-primary-disabled);
  --cmp-button-primary-bg-hover: var(--sem-color-bg-primary-hover);
  --cmp-button-primary-bg-loading: var(--sem-color-bg-primary-background);
  --cmp-button-primary-fg-default: var(--sem-color-fg-neutral-contrast);
  --cmp-button-primary-fg-disabled: var(--sem-color-fg-neutral-disabled);
  --cmp-button-secondary-bg-active: var(--sem-color-bg-neutral-active);
  --cmp-button-secondary-bg-default: transparent;
  --cmp-button-secondary-bg-disabled: transparent;
  --cmp-button-secondary-bg-hover: var(--sem-color-bg-neutral-hover);
  --cmp-button-secondary-bg-loading: var(--sem-color-bg-neutral-default);
  --cmp-button-secondary-border-default-color: var(--sem-color-border-default);
  --cmp-button-secondary-border-default-style: solid;
  --cmp-button-secondary-border-default-width: 1px;
  --cmp-button-secondary-border-disabled-color: var(--sem-color-border-subtle);
  --cmp-button-secondary-border-disabled-style: solid;
  --cmp-button-secondary-border-disabled-width: 1px;
  --cmp-button-secondary-text-default: var(--sem-color-fg-neutral-default);
  --cmp-button-secondary-text-disabled: var(--sem-color-fg-neutral-disabled);
  --cmp-button-spacinghorizontal: var(--sem-spacing-medium);
  --cmp-button-tertiary-default: var(--sem-color-fg-neutral-contrast);
  --cmp-button-tertiary-disabled: var(--sem-color-fg-neutral-disabled);
  --cmp-sidenav-borderradius: var(--sem-borderradius-large);
  --cmp-sidenav-chapter-bg-active: var(--sem-color-bg-neutral-active);
  --cmp-sidenav-chapter-bg-default: transparent;
  --cmp-sidenav-chapter-bg-hover: var(--sem-color-bg-neutral-hover);
  --cmp-sidenav-chapter-bg-selected: var(--sem-color-bg-primary-background);
  --cmp-sidenav-chapter-fg-default: var(--sem-color-fg-neutral-default);
  --cmp-sidenav-chapter-fg-disabled: var(--sem-color-fg-neutral-disabled);
  --cmp-sidenav-chapter-gap: var(--sem-spacing-small);
  --cmp-sidenav-chapter-height: var(--sem-height-xlarge);
  --cmp-sidenav-gap: var(--sem-spacing-xsmall);
  --cmp-sidenav-item-bg-default: transparent;
  --cmp-sidenav-item-bg-hover: var(--sem-color-bg-neutral-hover);
  --cmp-sidenav-item-bg-pressed: var(--sem-color-bg-neutral-active);
  --cmp-sidenav-item-bg-selected: var(--sem-color-bg-primary-background);
  --cmp-sidenav-item-borderradius: var(--sem-borderradius-large);
  --cmp-sidenav-item-fg-default: var(--sem-color-fg-neutral-default);
  --cmp-sidenav-item-fg-disabled: var(--sem-color-fg-neutral-disabled);
  --cmp-sidenav-item-gap: var(--sem-spacing-small);
  --cmp-sidenav-item-height: var(--sem-height-xlarge);
  --cmp-sidenav-item-spacex: var(--sem-spacing-medium);
  --cmp-sidenav-spacex: var(--sem-spacing-xsmall);
  --cmp-sidenav-spacey: var(--sem-spacing-xsmall);
  --cmp-tab-borderradius: var(--sem-borderradius-large);
  --cmp-tab-gap: var(--sem-spacing-xsmall);
  --cmp-tab-height: var(--sem-height-xxlarge);
  --cmp-tab-spacinghorizontal: var(--sem-spacing-medium);
  --cmp-tab-strip-height: 3px;
  --cmp-tab-strip-selected: var(--sem-color-fg-neutral-contrast);
  --cmp-tab-strip-unselected: transparent;
  --cmp-tab-unselected-bg-active: var(--sem-color-bg-neutral-active);
  --cmp-tab-unselected-bg-default: transparent;
  --cmp-tab-unselected-bg-hover: var(--sem-color-bg-neutral-hover);
}

h1 {
  color: var(--sem-color-fg-neutral-contrast);
  font-family: var(--core-fontfamily-sans);
  font-size: var(--core-fontsize-h1);
  font-weight: var(--core-fontweight-semibold);
  letter-spacing: -0.02em;
  line-height: var(--core-lineheight-headline);
}

h2 {
  color: var(--sem-color-fg-neutral-contrast);
  font-family: var(--core-fontfamily-sans);
  font-size: var(--core-fontsize-h2);
  font-weight: var(--core-fontweight-semibold);
  letter-spacing: -0.02em;
  line-height: var(--core-lineheight-headline);
}

p {
  color: var(--sem-color-fg-neutral-contrast);
  font-family: var(--core-fontfamily-sans);
  font-size: var(--core-fontsize-body);
  font-weight: var(--core-fontweight-regular);
  line-height: var(--core-lineheight-paragraph);
}

code {
  background-color: #F0F2F0;
  border: 1px solid var(--sem-color-border-subtle);
  border-radius: 2px;
  color: var(--sem-color-fg-neutral-contrast);
  font-family: var(--core-fontfamily-mono);
  font-size: var(--core-fontsize-body);
  font-weight: var(--core-fontweight-regular);
  line-height: var(--core-lineheight-headline);
  padding: 2px 0.3em;
  font-size: 16px;
}

mark {
  background-color: var(--green-50);
  color: black;
  padding: 0.1em 0.2em;
}

.neo-splitter {
  opacity: 1;
  margin: 8px 2px;
  border-radius: 100px;
  border: unset !important;
}
.neo-splitter:hover, .neo-splitter:active {
  background-color: #5595F5 !important;
}

.neo-dragproxy.neo-splitter {
  background-color: #5595F5 !important;
}